
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    // 标题插槽
    title: {
      type: String,
      default: ''
    },
    // 数据信息
    listData: {
      type: Array,
      required: true
    },
    // 数据总数
    listCount: {
      type: Number,
      default: 0
    },
    // 属性信息
    propList: {
      type: Array,
      required: true
    },
    // 是否显示序号列
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    // 是否显示选择框列
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    // 获取page,为分页器做准备
    page: {
      type: Object,
      default: () => ({ currentPage: 1, pageSize: 10 })
    },
    // 是否还有子属性
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    // 是否显示footer：主要是分页内容
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ['selectionChange', 'update:page'],
  setup(props, { emit }) {
    // 处理选择框
    const handleSelectionChange = (value: any) => {
      emit('selectionChange', value)
    }

    // 分页器操作
    const handleCurrentChange = (currentPage: number) => {
      emit('update:page', { ...props.page, currentPage })
    }

    const handleSizeChange = (pageSize: number) => {
      emit('update:page', { ...props.page, pageSize })
    }

    return {
      handleSelectionChange,
      handleCurrentChange,
      handleSizeChange
    }
  }
})
